body {
    background: url("../../../assets/img/background.jpg") repeat-x fixed left top;
    background-color:#6d9ac1;
    }

#wrap {
    background:@blueLighter;
    padding-top:15px;
.maincolumn   {
    border-bottom:1px solid @blue;
    background:white;
    }
}

// END
// fonts
@font-face {
    font-family: DGUVMetaWeb-Bold;
    src: url("../fonts/DGUVMetaWeb-Bold.eot");
}
@font-face {
    font-family: DGUVMetaWeb-Bold;
    src: url("../fonts/DGUVMetaWeb-Bold.woff") format("woff");
}
@font-face {
    font-family: DGUVMetaWeb-Normal;
    src: url("../fonts/DGUVMetaWeb-Normal.eot");
}
@font-face {
    font-family: DGUVMetaWeb-Normal;
    src: url("../fonts/DGUVMetaWeb-Normal.woff") format("woff");
}
@dguv-bold-font-name: "DGUVMetaWeb-Bold";
@dguv-normal-font-name: "DGUVMetaWeb-Normal";

// general
@media (max-width: 1200px) {
.nvaEventTitle .col-md-10 {
    width:100%;
    }
.nvaEventContent.col-md-offset-2 {
    margin-left:0;
    }
}


.table-responsive caption,
.table-striped caption,
#parent-fieldname-description,
.documentDescription,
h1, h2, h3, h4 {
    font-family: @dguv-bold-font-name, sans-serif;
}
.documentByLine {padding:10px 0}
h4 {line-height:1.4em;}
h1 {color:@blue;}
#parent-fieldname-text h1 {
    background-color:@blue;
    border-bottom:1px solid @blue;
    margin:0;
    padding:5px 5px 0 5px;
    color:@white;
    font-size:@font-size-h4;
    }
p, ul, 
.panel-heading h4 {
    font-family: @dguv-normal-font-name, sans-serif;
}
ul.listTypeSquare {
    list-style-type: disc !important;
    padding-left:5px !important;
    li {padding-left:0px;text-indent: -0px !important;}
    }
.panel-heading h4 {
    color:white;
    }
.documentDescription {
    margin:10px 0;
    }
.newsDescription {
    font-family:@dguv-bold-font-name;
    text-decoration:unterline;
}
.newsImageContainer {
    float: left;
        margin-right: 20px;
        margin-bottom: 10px;
    width:38%; //col-span-4
    }
.actionMenuSelected,
#edit-bar {
    background:darkgrey;
.state-published {
    background-color:@cyan;
}
.active {
    background:ivory;
}
}

.breadcrumb {
    padding-top: 5px;
    margin-bottom: 0px;
    margin-right:5px;
    }
.list-group {
    margin-bottom: 10px;
}
a.newsItem:hover,
.media:hover {background: @lightGrayBgColor;}

// Lars 20.09.2017 needed

.discreet {
  color: grey;
  font-size: 80%;
}

#content .callout {
    background: #eee;
    padding: 1em;
    border-left: 1em solid #ccc;
    clear: both;
}

// Ende Lars 20.09.2017 needed

a.tile {
    width: 24px;
    height: 24px;
    background-position: -20px -318px;
}
// kurzfassung
.media {
    margin-top:0;
    .description, .bgetem_folder_description {color:@gray-dark;}
}
.media-default {
    margin-top: 0px;
    margin-bottom: 10px;
    border-top: 1px dotted @collapseBg;
}
// viewlets
#viewlet-below-content-body {clear:both;}
// navbar
.navbar-default {
    border:0;
//	margin-right:4px;
    }
.navbar {
    margin-bottom: 0px;
    }
.navbar .nav > li {
    border-right: 1px solid white;
    }
.navbar .nav > li > a {
    padding: 4px 8px 6px;
    }
.navbar .nav > li > {
    a {
        padding: 4px 8px 6px;}
    }
.navbar .nav > li:first-child {
    margin-left:-6px;}
.navbar-default .navbar-inner {
    background-color: @blue;
    background-image: none;
border-color: @blue;
filter:none;
}
.navbar-default .brand {
    color: white;
    }
.navbar-fixed-top .navbar-inner,
.navbar-static-top .navbar-inner {
    border-width: 0;
    }
.navbar-default .nav li:hover,
.navbar-default .nav li.selected,
.navbar-default .nav .active > a,
.navbar-default .nav .active > a:hover,
.navbar-default .nav .active > a:focus {
    background-color: @bluehover;
        -webkit-box-shadow: none;
        -moz-box-shadow: none;
        box-shadow: none;
}
.navbar-default .navbar-toggle {
    float: left;
    margin-left: 15px;
    margin-top: 5px;
    padding:2px;
    margin-bottom:5px;
    line-height:4px;
    border-radius:0;
        }

@media (max-width: @screen-sm-max) {
.navbar-toggle:after {
    content: 'Rubriken';
    position: relative;
    top: -6px;
    margin-left: 40px;
    color:@white;
    }
}

@media (max-width: 1190px) {
    .navbar-nav > li > a {
    font-size:11px;
    }
}
@media (max-width: 780px) {
    .container-fluid {padding-right:0;padding-left:0;}
    .navbar-nav > li > a {
    font-size:14px;
    }
}
@media (max-width: @screen-md) {
    #side-menu {
        background: @blueLighter;
        border:1px solid #eee;
        -webkit-box-shadow: 3px 3px 5px 0px;
        -moz-box-shadow: 3px 3px 5px 0px;
        box-shadow: 3px 3px 5px 0px;
        ul {
            list-style-type: none;
            display:block;
            margin-right:5px;
            margin-left:-15px !important;
        }
        li.navTreeItem a:hover {color:white !important;}
        .navTreeLevel0 li.navTreeItem:last-child {border-top: 1px solid #004994;}
    }
}
@media (max-width: 768px) {
    .media-object {padding-bottom:10px}
    #viewlet-below-content {display:none;}
