.navMenu {
    //background:@white;
    line-height:30px;
    font-weight:bold;
    padding-left:10px;
    }
    .navMenu a {
    color:@blue !important;
    }
    li.navTreeItem {
        border-top: 1px solid @white;
        padding-top:10px;
        padding-bottom:10px;
            a {
            color:@white;
            &:hover {color:@white !important;text-decoration:underline}
            }
        &:hover {background-color:#094079;}
        }
        .navbar .nav>li:first-child {
        margin-left: 0px !important;
    }
    .navbar .nav>li {
        border-right: 0 !important;
    }
    .navbar-nav {
        margin: 0 -3px 0 0;
    li {border-bottom:1px solid #094079}
        }
    .navbar-collapse {
        padding-left: 0px !important;
        padding-right: -4px !important;
    }
    .navbar .nav>li a {
        padding: 10px 8px 10px;
    }
    .btn.btn-info {border:@border-radius-none}
}
@media (max-width: 991px) {
    .fa-plus-square:before {
        padding-right: 10px;
    }
}

//carousel
.carousel-caption {
    padding: 6px;
    background: rgba(255, 255, 255, 0.8);
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    text-align: left;
}

.carousel-caption p {
    margin-right:100px;
    color: #333;
}
.carousel-indicators {
    position: absolute;
    top: 10px;
    right: 10px;
    z-index: 5;
    margin: 0;
    list-style: none;
}
.carousel-control {
    top: 40%;
    width: 40px;
    height: 40px;
    margin-top: -20px;
    font-size: 36px;
    line-height: 30px;
    text-align: center;
    opacity: 0.8;
    filter: alpha(opacity=80);
    z-index:999;
    padding-left:5px;
    padding-right:5px;
}
.carousel-control.left,
.carousel-control.right {background-image:none;}

// breadcrumbs
#portal-breadcrumbs span {
    color:@fontLighter;
    }
.breadcrumb {
    border-radius: 0px;
    margin-right: 10px;
    margin-bottom: 5px !important;
    padding-left:10px !important;
    }
// site-action nav
#site-action {
    padding-right:0px;
}
#site-action .navbar .nav > li > a {
    color: @blue !important;
    padding:0 !important;
    }
#site-action .navbar .nav > .active > a {
    color: @blue;
    }
#site-action .nav  {
    margin-bottom:0;
    }
#site-action ul  {
    margin:10px 10px;
    }
li.divider-vertical:last-child {
    display:none;
}
.divider-vertical {
    border-left: 1px solid @blue;
    border-right: 1px solid @blueLighter;
    height: 12px;
    margin: 2px 4px;
}
.navbar .divider-vertical {
    border-left: 1px solid @blue;
    border-right: 1px solid @blueLighter;
    height: 12px;
    margin: 2px 4px;
}

